        "treaty__text": "Online store-website on the Internet at reface.tech\nThe seller is the legal representative of the online store.\nAgreement – this document\nPrivacy policy – document located at reface. tech/policy\nThe user – customer of the online store\nProduct – items not yet available for sale\nPre-order – the customer's intention to purchase an online store product after an indefinite period of time. This intention is expressed by the customer by sending an email via the form on the online store's website.\n\nBy using the site, the client agrees to this agreement and the site's privacy policy,. If the client does not agree with these documents, they must suspend their use of the site\nThe agreement and privacy policy may change without notice to customers.\nPre-order can be canceled by both the Customer and the online store. If the pre-order is canceled by the online store, the cancellation may be made without explanation. If the customer cancels the pre-order, the online store may require an explanation of the reason for canceling the pre-order, provided in writing (a letter to the online store's email address).\nThe information published on the pages of the online store, with the exception of this Agreement, is for informational purposes only, and under no circumstances is a public offer.\nInformation about prices, configuration, and delivery dates is limited by the number of pre-Orders and time of validity. For detailed and up-to-date information, as well as to confirm the terms of the pre-Order, please contact the Seller or its authorized representatives directly.\nThe online store has the right to notify the Customer when the Goods go on sale. The customer has the right to ask the online store for the approximate time of receipt of the Product for sale, but the online store has the right not to respond.\nAll text information and graphic images on the Seller's Website are the property of the Seller and/or its contractors.",
