        "policy__text": "¡hola!\n\nNosotros recogemos información sobre nuestros usuarios. A continuación se enumeran algunos ejemplos (pero no todos ellos):\n\n— Idioma. Para conocer el idioma a utilizar para mantenerse en contacto con usted.\n— Email. Para saber cómo mantener en contacto con usted.\n— Hora en que se envió de correo electrónico.\n— Los resultados de la encuesta social. Para analizar nuestro público.\n— Versión de aterrizaje. Para entender el contexto.\n\nUtilizamos sus datos sólo para el desarrollo de nuestro producto.\n\nPodemos modificar esta política de privacidad cuando queremos. Si utiliza los productos de revestir de nuevo (incluyendo el sitio, aplicaciones, etc.), que están de acuerdo con esta política de privacidad.",
