        "reface__diagnostics__title": "Acerca del dispositivo",
        "reface__retraining__description": "Si sus gestos se interpretan incorrectamente por el clasificador, se puede aprender de nuevo.\nHay dos modos de reconversión disponibles: manual y automático.\nen el modo manual, que vaya a grabar segmentos cortos de vídeo e indicar donde se hizo el gesto. El equipo lo hará automáticamente para usted.",
