        "reface__actions__description": "La liste des actions disponibles sera élargie. Actuellement, il existe une intégration avec IFTTT via Webhook, ainsi que la possibilité d'ajouter des actions par des développeurs tiers via API (par exemple, \"cliquez sur un bouton\").",
