        "reface__diagnostics__title": "A propos de l'appareil",
        "reface__retraining__description": "Si vos gestes sont mal interprétés par le classificateur, vous pouvez les réapprendre.\nDeux modes de recyclage sont disponibles: manuel et automatique.\nEn mode manuel, vous allez enregistrer de courts segments de vidéo et indiquer où le geste a été fait. L'ordinateur le fera automatiquement pour vous.",
