        "policy__subtitle": "18 dicembre 2019",
        "policy__text": "Ciao!\nRaccogliamo informazioni sui nostri utenti. Qui sono elencati alcuni esempi (ma non tutti):\n— Linguaggio. Per sapere quale lingua utilizzare per rimanere in contatto con te.\n— E-mail. Per sapere come rimanere in contatto con te.\n— Ora in cui hai inviato l'e-mail.\n— I risultati del sondaggio sociale. Analizzare il nostro pubblico.\n— Versione di atterraggio. Per capire il contesto.\nUtilizziamo i tuoi dati solo per sviluppare il nostro prodotto.\nPossiamo modificare questa politica sulla privacy quando vogliamo. Se usi i prodotti di Reface (inclusi sito, app ecc.), Sei d'accordo con questa politica sulla privacy.",
        "policy__title": "Politica sulla riservatezza",
        "presentation__button_preorder": "Preordinare",
        "reface__actions__description": "L'elenco delle azioni disponibili sarà ampliato. Attualmente, v'è l'integrazione con IFTTT via Webhook, così come la possibilità di aggiungere le azioni da sviluppatori di terze parti tramite API (ad esempio, \"fare clic su un pulsante\").",
