        "reface__computer_connection__description": "È possibile collegare un computer e gestirlo.\nIl computer deve essere sulla stessa rete WiFi del telefono.",
        "reface__computer_connection__title": "Collegamento al computer",
        "reface__diagnostics__description": "È possibile visualizzare le informazioni circa l'esattezza degli elettrodi, così come l'ultimo gesto riconosciuto.",
        "reface__diagnostics__title": "A proposito di dispositivo",
        "reface__retraining__description": "Se i vostri gesti sono erroneamente interpretate dal classificatore, si può imparare di nuovo.\nSono disponibili due modalità di riqualificazione disponibili: manuale e automatico.\nIn modalità manuale, si registra brevi segmenti di video e di indicare dove è stato fatto il gesto. Il computer verrà automaticamente fare questo per voi.",
