        "policy__text": "Мы собираем о пользователях данные для улучшения качества продукта. Ниже перечислены некоторые из этих данных:\n\n— Язык. Чтобы знать, на каком языке писать новости.\n— Емейл. Чтобы знать, куда писать.\n— Время отправки емейла.\n— Ответы из соцопросов. Чтобы лучше понимать целевую аудиторию.\n— Версия сайта. Чтобы понимать контекст.\n— Всякие метрики вроде Гугл Аналитики и Яндекс Метрики.\n\nМы никак не используем эти данные, кроме как по прямому назначению: для нужд проекта.\n\nПолитика конфиденциальности может меняться нами без предупреждения пользователей. Если вы используете сайт и продукты Reface, вы соглашаетесь с этой политикой конфиденциальности.",
