        "reface__retraining__description": "Если ваши жесты неправильно определяются классификатором, вы можете его перобучить.\nДоступны два режима переобучения: ручной и автоматический.\nВ ручном режиме вы будете записывать короткие отрезки видео и указывать, где был произведен жест. В автоматическом за вас это сделает компьютер.",
