        "treaty__subtitle": "19 декабря 2019",
        "treaty__text": "Интернет-магазин – сайт в сети интернет по адресу reface.tech\nПродавец – законный представитель Интернет-магазина.\nДоговор – данный документ\nПолитика конфиденциальности –  документ, расположенный по адресу reface.tech/policy\nКлиент – пользователь Интернет-магазина\nТовар – еще не поступившие в продажу вещи\nПредзаказ – намерение клиента приобрести товар Интернет-магазина, когда  через неопределенный период времени. Данное намерение высказывается клиентом путем отправки своего емейла через форму на сайте Интернет-магазина.\n\nИспользуя сайт, клиент соглашается с данным договором и политикой конфиденциальности сайта,. Если клиент не согласен с данными документами, он должен приостановить свое пользование сайтом\nДоговор и политика конфиденциальности могут изменяться без предупреждения клиентов\nПрезаказ может быть отменен как со стороны Клиента, так и со стороны Интернет-магазина. В случае отмены предзаказа со стороны Интернет-магазина отмена может производиться без объяснения причин. В случае отмены предзаказа со стороны Клиента Интернет-магазин может потребовать объяснение причины отмены предзаказа, изложенное в письменной форме (письмо на емейл Интернет-магазина).\nДоговор является публичной офертой в соответствии со ст.435 и ч.2 ст.437 ГК РФ.\nИнформация, опубликованная на страницах Интернет-магазина, за исключением настоящего Договора, носит исключительно ознакомительный характер, и ни при каких условиях не являются публичной офертой, определяемой положениями ст. 437 ГК РФ.\nИнформация о ценах, комплектации и сроках поставки ограничена по количеству Предзаказов и времени действия. Для получения подробной и актуальной информации, а также для подтверждения условий Предзаказа следует обращаться непосредственно к Продавцу или его уполномоченным представителям.\nИнтернет-магазин вправе уведомить Клиента, когда Товары поступят в продажу. Клиент вправе спрашивать у Интернет-магазина приблизительные сроки поступления в продажу Товара, но Интернет-магазин вправе не отвечать.\nВся текстовая информация и графические изображения, находящиеся на Сайте Продавца, являются собственностью Продавца и/или его контрагентов.",
